/**
 * ECIES encrypt/decrypt with Ethereum keys
 * Modified from https://github.com/vhpoet/simple-ecies/blob/master/index.js
 */
"use strict";

var Crypto = require("crypto");

var EC = require("elliptic").ec;

var ec = new EC("secp256k1");

var Buffer = require('safe-buffer').Buffer;
/**
 * AES-256 CBC encrypt
 * @param {Buffer} iv
 * @param {Buffer} key
 * @param {Buffer} plaintext
 * @returns {Buffer} ciphertext
 */


var AES256CbcEncrypt = function AES256CbcEncrypt(iv, key, plaintext) {
  var cipher = Crypto.createCipheriv("aes-256-cbc", key, iv);
  var firstChunk = cipher.update(plaintext);
  var secondChunk = cipher.final();
  return Buffer.concat([firstChunk, secondChunk]);
};
/**
 * AES-256 CBC decrypt
 * @param {Buffer} iv
 * @param {Buffer} key
 * @param {Buffer} ciphertext
 * @returns {Buffer} plaintext
 */


var AES256CbcDecrypt = function AES256CbcDecrypt(iv, key, ciphertext) {
  var cipher = Crypto.createDecipheriv("aes-256-cbc", key, iv);
  var firstChunk = cipher.update(ciphertext);
  var secondChunk = cipher.final();
  return Buffer.concat([firstChunk, secondChunk]);
};
/**
 * Compares if two buffers are equal
 * @param {Buffer} b1
 * @param {Buffer} b2
 * @returns {boolean} true if the buffers are equal
 */


var BufferEqual = function BufferEqual(b1, b2) {
  if (b1.length !== b2.length) {
    return false;
  }

  var res = 0;

  for (var i = 0; i < b1.length; i++) {
    res |= b1[i] ^ b2[i];
  }

  return res === 0;
};
/**
 * ECIES encrypt
 * @param {Buffer} pubKeyTo Ethereum pub key, 64 bytes
 * @param {Buffer} plaintext Plaintext to be encrypted
 * @param {?{?iv: Buffer, ?ephemPrivKey: Buffer}} opts
 * optional iv (16 bytes) and ephem key (32 bytes)
 * @returns {Buffer} Encrypted message, serialized, 113+ bytes
 */


var Encrypt = function Encrypt(pubKeyTo, plaintext, opts) {
  opts = opts || {};
  var ephemPrivKey = ec.keyFromPrivate(opts.ephemPrivKey || Crypto.randomBytes(32));
  var ephemPubKey = ephemPrivKey.getPublic();
  var ephemPubKeyEncoded = Buffer.from(ephemPubKey.encode()); // Every EC public key begins with the 0x04 prefix before giving
  // the location of the two point on the curve

  var px = ephemPrivKey.derive(ec.keyFromPublic(Buffer.concat([Buffer.from([0x04]), pubKeyTo])).getPublic());
  var hash = Crypto.createHash("sha512").update(px.toArrayLike(Buffer)).digest();
  var iv = opts.iv || Crypto.randomBytes(16);
  var encryptionKey = hash.slice(0, 32);
  var macKey = hash.slice(32);
  var ciphertext = AES256CbcEncrypt(iv, encryptionKey, plaintext);
  var dataToMac = Buffer.concat([iv, ephemPubKeyEncoded, ciphertext]);
  var mac = Crypto.createHmac("sha256", macKey).update(dataToMac).digest();
  var serializedCiphertext = Buffer.concat([iv, // 16 bytes
  ephemPubKeyEncoded, // 65 bytes
  mac, // 32 bytes
  ciphertext]);
  return serializedCiphertext;
};
/**
 * ECIES decrypt
 * @param {Buffer} privKey Ethereum private key, 32 bytes
 * @param {Buffer} encrypted Encrypted message, serialized, 113+ bytes
 * @returns {Buffer} plaintext
 */


var Decrypt = function Decrypt(privKey, encrypted) {
  // read iv, ephemPubKey, mac, ciphertext from encrypted message
  var iv = encrypted.slice(0, 16);
  var ephemPubKeyEncoded = encrypted.slice(16, 81);
  var mac = encrypted.slice(81, 113);
  var ciphertext = encrypted.slice(113);
  var ephemPubKey = ec.keyFromPublic(ephemPubKeyEncoded).getPublic();
  var px = ec.keyFromPrivate(privKey).derive(ephemPubKey);
  var hash = Crypto.createHash("sha512").update(px.toArrayLike(Buffer)).digest();
  var encryptionKey = hash.slice(0, 32);
  var macKey = hash.slice(32);
  var dataToMac = Buffer.concat([iv, ephemPubKeyEncoded, ciphertext]);
  var computedMac = Crypto.createHmac("sha256", macKey).update(dataToMac).digest(); // verify mac

  if (!BufferEqual(computedMac, mac)) {
    throw new Error("MAC mismatch");
  }

  var plaintext = AES256CbcDecrypt(iv, encryptionKey, ciphertext);
  return plaintext;
};

module.exports = {
  encrypt: Encrypt,
  decrypt: Decrypt
};